(function () {

    var $contribute = $("#contribute");
    var $scrollUpBtn = $('#scroll-up-btn');

    $scrollUpBtn.click(function () {

        $('body,html').animate({ scrollTop: 0 }, 1500);
    });

    $(window).on("scroll", function () {
        var $section = $(".infographic-section");
        var offset = void 0;
        if ($section.length === 0) {
            return;
        } else {
            offset = $section.offset().top - 400;
        };

        var $window = $(window).scrollTop();
        if ($window >= offset) {
            $contribute.removeClass("animate-hidden animated fadeOut").addClass("animate-visible animated fadeIn");
        } else {
            $contribute.removeClass("animate-visible animated fadeIn").addClass("animate-hidden animated fadeOut");
        }
        if ($(this).scrollTop() != 0) {

            $scrollUpBtn.removeClass("fadeOut").addClass("fadeIn");
        } else {
            $scrollUpBtn.removeClass("fadeIn").addClass("fadeOut");;
        }
    });

    $(window).on("load", function () {
        $(".header-charts").addClass("active");
    });
})();
