(function () {
  // set initial values
  var curr_list = ["ZNAQ/USD", "ZNAQ/BTC", "ZNAQ/ETH", "ZNAQ/LTC", "ZNAQ/ETC", "ZNAQ/BCH", "ZNAQ/XRP", "ZNAQ/DASH", "ZNAQ/XMR", "ZNAQ/NEO", "ZNAQ/IOT", "ZNAQ/OMG", "ZNAQ/ETP", "ZNAQ/EOS", "ZNAQ/XLM", "ZNAQ/ADA", "ZNAQ/XEM", "ZNAQ/TRX", "ZNAQ/USDT", "ZNAQ/SC"];

  var setCurrency = function setCurrency(resp, list, container) {
    list.forEach(function (item) {
      if (resp["currency"][item][1] === null) {
        var color = 'green';
      } else {
        var color = resp["currency"][item][1];
      }

      container.append("<div class='block " + color + "'><p>" + item + "</p><p>" + resp["currency"][item][0] + "</p></div>");
    });
  };

  if (!localStorage['currencies_data_new']) {
    $.ajax({
      type: "GET",
      url: "/currencies",
      success: function success(response) {
        $('.participate .cost').text("1 ZNAQ = " + parseFloat(response['currency']['ZNAQ/USD'][0]).toFixed(2) + " USD");
        var currency_container = $('.curses-section .content');
        setCurrency(response, curr_list, currency_container);
        localStorage['currencies_data_new'] = JSON.stringify(response);
      }
    });
  } else {
    $('.participate .cost').text("1 ZNAQ = " + parseFloat(JSON.parse(localStorage['currencies_data_new'])['currency']['ZNAQ/USD'][0]).toFixed(2) + " USD");
    setCurrency(JSON.parse(localStorage['currencies_data_new']), curr_list, $('.curses-section .content'));
  }
  var ajax_call = function ajax_call() {
    if (localStorage['currencies_data_new']) {
      localStorage.removeItem('currencies_data_new');
    }
    $.ajax({
      type: "GET",
      url: "/currencies",
      success: function success(response) {
        // $('.participate .cost').text("1 ETH = " + response['token_count'].toFixed() + " TOK");
        $('.participate .cost').text("1 ZNAQ = " + parseFloat(response['currency']['ZNAQ/USD'][0]).toFixed(2) + " USD");
        var currency_container = $('.curses-section .content');
        currency_container.empty();
        setCurrency(response, curr_list, currency_container);
        localStorage['currencies_data_new'] = JSON.stringify(response);
      }
    });
  };

  var interval = 1000 * 30 * 1; // every 10 secs

  // ajax_call();

  setInterval(ajax_call, interval);
})();
