(function () {
  var $form = $(".donation-form");
  var $submit = $form.find(".send-btn");

  $submit.off("click");
  $submit.on("click", function (e) {

    e.preventDefault();
    $.ajax({
      url: $form.attr('action'),
      type: "POST",
      data: $form.serialize(),
      success: function success(response) {
        if (response["transaction_error"]) {
          if (response['transaction_error'].e20_wallet) {
            var $e20_wallet_tooltip = $('#e20_wallet_errors');
            $e20_wallet_tooltip.text(response['transaction_error'].e20_wallet[0]);
            $e20_wallet_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }
          if (response['transaction_error'].payment_method) {
            var $transaction_tooltip = $('#transaction_errors');
            $transaction_tooltip.text(response['transaction_error'].payment_method[0]);
            $transaction_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }
          if (response['transaction_error'].tx_id) {
            var $tx_id_tooltip = $('#tx_id_errors');
            $tx_id_tooltip.text(response['transaction_error'].tx_id[0]);
            $tx_id_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }
        }
        if (response['message']) {
          var $transaction_tooltip = $('#transaction_errors');
          $transaction_tooltip.text(response['message']);
          $transaction_tooltip.fadeIn('fast').delay(2000).fadeOut('slow', function () {
            $('.step--2').addClass('inactive');
            $('.step--3').addClass('inactive');
          });
          $form[0].reset();
        }
        if (response['donation_obj']) {
          console.log(response['donation_obj']['date']);
          console.log(response['donation_obj']['method']);
          var donationRow = "\n                    <tr>\n                      <td class=\"date\">" + response['donation_obj']['date'] + "</td>\n                      <td class=\"method\">" + response['donation_obj']['method'] + "</td>\n                      <td class=\"tokensum\">Pending</td>\n                      <td class=\"totalsum\"></td>\n                    </tr>\n                  ";
          $('.transactions tbody').prepend(donationRow);
        }
      },
      error: function error(xhr, status, _error) {
        var response = JSON.parse(xhr.responseText);
        console.log("error response: ", response);
      }
    });
  });
})();
