(function () {
    $(function () {
        if (window.location.pathname.split("/")[1] == "ru") {
            var monthNames = ["", "", "", "", "", "", "", "", "", "", "", ""];
        } else {
            var monthNames = ["Jan", "Feb", "March", "Apr", "May", "June", "July", "Aug", "Sep", "Oct", "Nov", "Dec"];
        }
        var handle = $("#custom-handle");
        var handle2 = $("#custom-handle2");
        var total = $('#total');
        var purchase = 0;
        var date = 1;
        var history = {
            "1": "0.177232", "2": "0.185177", "3": "0.223249",
            "4": "0.258958", "5": "0.372463", "6": "0.827843",
            "7": "0.999651", "8": "0.921627", "9": "1.73817",
            "10": "1.47465", "11": "1.81825", "12": "2.97165"
        };
        $("#ui-slider").slider({
            min: 0,
            max: 10000,
            range: 'min',
            animate: true,
            create: function create() {
                handle.text("$0");
            },
            slide: function slide(event, ui) {
                handle.text("$" + ui.value);
                purchase = ui.value;
                if (!sessionStorage['cap_data']) {
                    $.ajax({
                        type: "GET",
                        url: "/cap",
                        success: function success(response) {
                            // total.text((((purchase / history[date]) / response["cap"]) + purchase).toFixed() + " $");
                            total.text((purchase / history[date] * response["cap"]).toFixed() + " $");
                            sessionStorage['cap_data'] = JSON.stringify(response["cap"]);
                        }
                    });
                } else {
                    total.text((purchase / history[date] * JSON.parse(sessionStorage['cap_data'])).toFixed() + " $");
                }
            }
        });
        $("#ui-slider2").slider({
            min: 0,
            max: 11,
            range: 'min',
            animate: true,
            create: function create() {
                handle2.html("<span>" + 1 + " " + monthNames[1] + "</span><span>2017</span>");
                total.text("0 $");
            },
            slide: function slide(event, ui) {
                handle2.html("<span>" + 1 + " " + monthNames[ui.value] + "</span><span>2017</span>");
                date = ui.value + 1;
                if (!sessionStorage['cap_data']) {
                    $.ajax({
                        type: "GET",
                        url: "/cap",
                        success: function success(response) {
                            // total.text((((purchase / history[date]) / response["cap"]) + purchase).toFixed() + " $");
                            total.text((purchase / history[date] * response["cap"]).toFixed() + " $");
                            sessionStorage['cap_data'] = JSON.stringify(response["cap"]);
                        }
                    });
                } else {
                    total.text((purchase / history[date] * JSON.parse(sessionStorage['cap_data'])).toFixed() + " $");
                }
            }
        });
    });
})();
