(function () {
    var processDelay;
    var bonuses;
    var ratio = parseFloat($('.discount').data('discount'));
    var totalEl = $('.total');

    $.ajax({
        type: "GET",
        url: "/en/bonuses",
        success: function success(response) {
            bonuses = response['bonuses'];

            if (sessionStorage['bonuses']) {
                sessionStorage.removeItem('bonuses');
            }

            sessionStorage.setItem('bonuses', JSON.stringify(bonuses));
        }
    });

    $('#calculator-value').on('change keyup paste', function () {
        clearTimeout(processDelay);
        processDelay = setTimeout(function () {
            var currency = $('select[name="calculator-currency"]').val();
            var value = $('#calculator-value').val();
            var tokens_input = $('#calculator-tokens');
            if (value && $.isNumeric(value) && currency) {
                if (!sessionStorage['calk_data']) {
                    $.ajax({
                        type: "GET",
                        url: "/en/currencies",
                        success: function success(response) {
                            tokens_input.val((parseFloat(value) * (1 / parseFloat(response['currency'][currency]))).toFixed(4));
                            sessionStorage.setItem('calk_data', JSON.stringify(response['currency']));
                        }
                    });
                } else {
                    tokens_input.val((parseFloat(value) * (1 / parseFloat(JSON.parse(sessionStorage.getItem('calk_data'))[currency]))).toFixed(4));
                }

                var totalVal = parseFloat(tokens_input.val()) * ratio;
                for (var i = 0; i < bonuses.length; i++) {
                    if (totalVal >= parseInt(bonuses[i][0])) {
                        totalEl.text((totalVal + totalVal / 100 * parseFloat(bonuses[i][1])).toFixed(4));
                        break;
                    } else {
                        // bonusEl.text();
                    }
                }
            } else {
                return;
            }
        }, 500);
    });
    $('select[name="calculator-currency"]').on('change', function () {
        clearTimeout(processDelay);
        processDelay = setTimeout(function () {
            var currency = $('select[name="calculator-currency"]').val();
            var value = $('#calculator-value').val();
            var tokens_input = $('#calculator-tokens');
            if (value && $.isNumeric(value) && currency) {
                if (sessionStorage['calk_data']) {
                    sessionStorage.removeItem('calk_data');
                }
                $.ajax({
                    type: "GET",
                    url: "/en/currencies",
                    success: function success(response) {
                        tokens_input.val((parseFloat(value) * (1 / parseFloat(response['currency'][currency]))).toFixed(4));
                        sessionStorage.setItem('calk_data', JSON.stringify(response['currency']));

                        var totalVal = parseFloat(tokens_input.val()) * ratio;
                        for (var i = 0; i < bonuses.length; i++) {
                            if (totalVal >= parseInt(bonuses[i][0])) {
                                totalEl.text((totalVal + totalVal / 100 * parseFloat(bonuses[i][1])).toFixed(4));
                                break;
                            } else {
                                // bonusEl.text();
                            }
                        }
                    }
                });
            } else {
                return;
            }
        }, 500);
    });
    $('#calculator-tokens').on('change keyup paste', function () {
        clearTimeout(processDelay);
        processDelay = setTimeout(function () {
            var currency = $('select[name="calculator-currency"]').val();
            var value_input = $('#calculator-value');
            var tokens_input_value = $('#calculator-tokens').val();
            if (tokens_input_value && $.isNumeric(tokens_input_value) && currency) {
                if (!sessionStorage['calk_data']) {
                    $.ajax({
                        type: "GET",
                        url: "/en/currencies",
                        success: function success(response) {
                            value_input.val((1 / (1 / parseFloat(response['currency'][currency]) / parseFloat(tokens_input_value))).toFixed(4));
                            sessionStorage.setItem('calk_data', JSON.stringify(response['currency']));
                        }
                    });
                } else {
                    value_input.val((1 / (1 / parseFloat(JSON.parse(sessionStorage.getItem('calk_data'))[currency]) / parseFloat(tokens_input_value))).toFixed(4));
                }

                var totalVal = parseFloat(tokens_input_value) * ratio;
                for (var i = 0; i < bonuses.length; i++) {
                    if (totalVal >= parseInt(bonuses[i][0])) {
                        // bonusEl.text(bonuses[i][1] + " %");
                        totalEl.text((totalVal + totalVal / 100 * parseFloat(bonuses[i][1])).toFixed(4));
                        break;
                    } else {
                        // bonusEl.text();
                    }
                }
            } else {
                return;
            }
        }, 500);
    });
})();
