        e.preventDefault();
        $modalWindow.css("display", "flex");
        $signinModal.show();
        $closeBtn.off("click");
        $closeBtn.on("click", function (e) {
            e.preventDefault();
            $modalWindow.css("display", "none");
            $signinModal.hide();
        });
