(function () {
  var $form = $(".pers-info-form");
  var $submit = $form.find(".submit");

  $submit.off("click");
  $submit.on("click", function (e) {

    e.preventDefault();

    $.ajax({
      url: $form.attr('action'),
      type: "POST",
      data: $form.serialize(),
      success: function success(response) {
        // console.log(response);
        if (response['user_errors']) {
          if (response['user_errors'].email) {
            var $email_tooltip = $('#email_errors');
            $email_tooltip.text(response['user_errors'].email[0]);
            $email_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }

          if (response['password_errors'].new_password1) {
            var $new_password1_tooltip = $('#new_password1_errors');
            $new_password1_tooltip.text(response['password_errors'].new_password1[0]);
            $new_password1_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }

          if (response['password_errors'].new_password2) {
            var $new_password2_tooltip = $('#new_password2_errors');
            $new_password2_tooltip.text(response['password_errors'].new_password2[0]);
            $new_password2_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }
        }

        if (response['password_errors']) {
          if (response['password_errors'].old_password) {
            var $old_password_tooltip = $('#old_password_errors');
            $old_password_tooltip.text(response['password_errors'].old_password[0]);
            $old_password_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }

          if (response['password_errors'].new_password1) {
            var $new_password1_tooltip = $('#new_password1_errors');
            $new_password1_tooltip.text(response['password_errors'].new_password1[0]);
            $new_password1_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }

          if (response['password_errors'].new_password2) {
            var $new_password2_tooltip = $('#new_password2_errors');
            $new_password2_tooltip.text(response['password_errors'].new_password2[0]);
            $new_password2_tooltip.fadeIn('fast').delay(2000).fadeOut('slow');
          }
          return;
        }

        if (response["message"]) {
          var $tooltip = $('#profile-tooltip');
          $tooltip.text(response['message']);
          $tooltip.fadeIn('fast').delay(1000).fadeOut('slow');
        }

        if (response["password_changed"]) {
          window.location.reload();
        }
      },
      error: function error(xhr, status, _error) {
        var response = JSON.parse(xhr.responseText);
        console.log("error response: ", response);
      }
    });
  });
})();
