(function () {
    var $pies = $(".graph-wrap");

    $pies.each(function () {
        var $mainImg = $(this).find(".main");
        var $imgs = $(this).find(".img");

        var $list = $(this).find(".graph-legend ul li");

        var _loop = function _loop(i) {
            $list.eq(i).off("mouseover");
            $list.eq(i).off("mouseout");
            $list.eq(i).on("mouseover", function () {
                $mainImg.hide();
                $imgs.eq(i).show();
            });
            $list.eq(i).on("mouseout", function () {
                $mainImg.show();
                $imgs.eq(i).hide();
            });
        };

        for (var i = 0; i < $list.length; i++) {
            _loop(i);
        }
    });
})();
