(function () {
    var $window = $("#modal-window");

    var $refModal = $("#ref-modal");
    var $refMoreBtn = $(".bounty-more");

    var $walletModal = $("#wallet-modal");
    var $walletMoreBtn = $(".wallet-more");

    if ($refModal.length == 0) {
        // pass
    } else {

        var modals = ['.terms-modal', '.advertisement-modal', '.wallet-modal'];

        $refMoreBtn.off("click");
        $refMoreBtn.on("click", function (e) {
            e.preventDefault();
            modals.forEach(function (item) {
                $(item).hide();
            });
            $window.addClass("open");
            var modal = $(this).data('modal');
            $refModal.show();
            $('.' + modal).show();
            var lastScrollPos = $(window).scrollTop();
            $('body,html').animate({ scrollTop: 0 }, 100);
            var $close = $refModal.find(".close");
            $close.off("click");
            $close.on("click", function (e) {
                e.preventDefault();
                $window.removeClass("open");
                $refModal.hide();
                $('.' + modal).hide();
                $('body,html').animate({ scrollTop: lastScrollPos }, 100);
            });
        });

        var wallets = ['.btc-modal', '.eth-modal', '.bch-modal', '.xrp-modal', '.ada-modal', '.xlm-modal', '.neo-modal', '.ltc-modal', '.eos-modal', '.xem-modal', '.dash-modal', '.etc-modal'];

        $walletMoreBtn.off("click");
        $walletMoreBtn.on("click", function (e) {
            e.preventDefault();
            wallets.forEach(function (item) {
                $(item).hide();
            });
            $window.addClass("open").removeClass("bounty-window").addClass("wallet-window");
            var modal = $(this).data('modal');
            $walletModal.show();
            $('.' + modal).show();
            var lastScrollPos = $(window).scrollTop();
            $('body,html').animate({ scrollTop: 0 }, 100);

            var $walletForm = $('.' + modal).find('form');
            var $submit = $walletForm.find('button');

            $submit.off("click");
            $submit.on("click", function (e) {
                e.preventDefault();
                var $walletInput = $walletForm.find('input[name="wallet"]');
                if ($walletInput.val()) {
                    $.ajax({
                        url: $walletForm.attr('action'),
                        type: "POST",
                        data: $walletForm.serialize(),
                        success: function success(response) {
                            var btnName = $submit.html();
                            $submit.html('Saved');
                            setTimeout(function () {
                                $walletModal.hide('fast');
                                $('.' + modal).hide('fast', function () {
                                    $window.removeClass("open").addClass("bounty-window").removeClass("wallet-window");
                                    $submit.html(btnName);
                                });
                            }, 1000);
                        },
                        error: function error(xhr, status, _error) {
                            var response = JSON.parse(xhr.responseText);
                            console.log("error response: ", response);
                        }
                    });
                }
            });

            var $close = $walletModal.find(".close");
            $close.off("click");
            $close.on("click", function (e) {
                e.preventDefault();
                $window.removeClass("open").addClass("bounty-window").removeClass("wallet-window");
                $walletModal.hide();
                $('.' + modal).hide();
                $('body,html').animate({ scrollTop: lastScrollPos }, 100);
            });
        });
    }
})();
