(function () {

    var $timers = $(".timer");
    var $bgline = $("#timer-bg-line");
    var date = new Date().getFullYear() + "/" + (new Date().getMonth() + 1) + "/" + new Date().getDate();

    var bgFunction = function bgFunction(deadline) {
        var date = new Date().getFullYear() + "/" + (new Date().getMonth() + 1) + "/1";
        console.log(date);
        console.log("12");
        var start = new Date(date);
        console.log(start);
        console.log("15");
        var current = new Date().getTime();
        console.log(current);
        console.log("18");

        var end = deadline;

        var point1 = current - start;
        console.log(point1);
        console.log("24");
        var point2 = deadline - start;
        console.log(point2);
        console.log("27");
        var result = point1 / point2; //Math.floor(result*100)
        console.log(result);
        console.log("30");

        $bgline.css("width", Math.floor(result * 100));
    };

    $timers.each(function () {
        var deadline = $(this).attr("data-time");

        if (deadline === undefined) {
            return;
        };

        var daysBlock = $(this).find("#days");
        var hoursBlock = $(this).find("#hours");
        var minutesBlock = $(this).find("#minutes");
        var secondBlock = $(this).find("#seconds");
        initializeClock(deadline, daysBlock, hoursBlock, minutesBlock, secondBlock);
    });

    function getTimeRemaining(endtime) {
        var oyear = endtime.split(" ")[0].split("/")[0];
        var omonth = endtime.split(" ")[0].split("/")[1] - 1;
        var oday = endtime.split(" ")[0].split("/")[2];
        var ohour = endtime.split(" ")[1].split(":")[0];
        var ominute = endtime.split(" ")[1].split(":")[1];
        var t = Date.parse(new Date(oyear, omonth, oday, ohour, ominute)) - Date.parse(new Date());
        var seconds = Math.floor(t / 1000 % 60);
        var minutes = Math.floor(t / 1000 / 60 % 60);
        var hours = Math.floor(t / (1000 * 60 * 60) % 24);
        var days = Math.floor(t / (1000 * 60 * 60 * 24));
        return {
            'total': t,
            'days': days,
            'hours': hours,
            'minutes': minutes,
            'seconds': seconds
        };
    }

    function initializeClock(endtime, daysBlock, hoursBlock, minutesBlock, secondBlock) {

        function updateClock() {
            var t = getTimeRemaining(endtime);
            daysBlock.text(('0' + t.days).slice(-2));
            hoursBlock.text(('0' + t.hours).slice(-2));
            minutesBlock.text(('0' + t.minutes).slice(-2));
            secondBlock.text(('0' + t.seconds).slice(-2));
            if (t.total <= 0) {
                clearInterval(timeinterval);
            }
        }

        updateClock();
        var timeinterval = setInterval(updateClock, 1000);
    }
})();
