(function () {
    var $btn = $("#video-btn");
    var $modalWindow = $(".modal-window");
    var $videomodal = $(".video-modal");
    var $close = $videomodal.find(".close");
    var $circles = $("#video-wrap").find(".video-circle");
    var $videos = $(".video-link");
    var $iframe = $videomodal.find("#video-frame");
    var deg = 1;
    var timer = void 0;
    var flaq = false;
    var url = "https://www.youtube.com/embed/";
    var play = "?rel=0&autoplay=1";
    var stop = "?rel=0&autoplay=0";

    var spin = function spin() {
        if (deg === 360) {
            deg = 0;
        }
        $circles.each(function () {
            $(this).css("transform", "rotate(" + deg + "deg)");
        });
        deg += 1;
    };
    $(document).ready(function () {
        $videos.hide();
        $btn.off("click");
        $btn.on("click", function () {
            $btn.toggleClass("active");
            $videos.toggle();
        });

        $("#video-wrap").on("mouseenter", function () {
            if (flaq) {
                clearInterval(timer);
                flaq = false;
            } else {
                timer = setInterval(function () {
                    flaq = true;
                    spin();
                }, 10);
            }
        });
        $("#video-wrap").on("mouseleave", function () {
            clearInterval(timer);
            flaq = false;
        });

        var $windowWidth = $(window).width();

        if ($windowWidth <= 400) {
            deg = 0;
            $("#video-wrap").off("mouseenter");
            $("#video-wrap").off("mouseleave");
            $circles.each(function () {
                $(this).css("transform", "rotate(0deg)");
            });
        }

        $(window).resize(function () {
            $windowWidth = $(window).width();
            if ($windowWidth <= 400) {
                deg = 0;
                $("#video-wrap").off("mouseenter");
                $("#video-wrap").off("mouseleave");
                $circles.each(function () {
                    $(this).css("transform", "rotate(0deg)");
                });
            } else {
                $("#video-wrap").off("mouseenter");
                $("#video-wrap").off("mouseleave");

                $("#video-wrap").on("mouseenter", function () {
                    if (flaq) {
                        clearInterval(timer);
                        flaq = false;
                    } else {
                        timer = setInterval(function () {
                            flaq = true;
                            spin();
                        }, 10);
                    }
                });
                $("#video-wrap").on("mouseleave", function () {
                    clearInterval(timer);
                    flaq = false;
                });
            }
        });

        $videos.each(function () {
            $(this).off("click");
            $(this).on("click", function (e) {
                var id = $(this).attr("href");
                e.preventDefault();
                $modalWindow.addClass("open");
                $videomodal.show();
                $iframe.attr("src", "" + (url + id + play));

                $close.off("click");
                $close.on("click", function (e) {
                    e.preventDefault();
                    $modalWindow.removeClass("open");
                    $videomodal.hide();
                    $iframe.attr("src", "" + (url + id + stop));
                });
            });
        });
    });
})();
